<template>
<div class='box'>
  <iframe ref='iframe' :src="iframeSrc" style="margin: 0;width: 100%;height: 100%;border: 0px solid #d0d0d0;" />
</div>
</template>

<script>
import Hammer from 'hammerjs';

export default {
  mounted () {
    this.iframeSrc = 'https://beautifulboys.github.io/test';
    // create a manager for that element
    // var mc = new Hammer.Manager(this.$refs.iframe);

    var mc = new Hammer.Manager(this.$refs.iframe);
    // create a recognizer
    var Pinch = new Hammer.Pinch();
    mc.set({enable: true});
    // add the recognizer
    mc.add(Pinch);

    // subscribe to events
    mc.on('pinch', function (e) {
      console.log(e);
    });
  },
  data () {
    return {
      iframeSrc: ''
    };
  },
  methods: {

  }
};
</script>

<style lang='less' scoped>
.box {
    width: 100%;
    height: 100%;
    background: #eee;
    box-sizing: border-box;
}
</style>



